<template>
  <div>
    <v-layout>
      <v-text-field type="text" placeholder="Type to filter..." v-model="filter" class="filter-field"></v-text-field>
      <v-btn @click.stop="editNode()">Edit</v-btn>
      <v-btn @click.stop="removeNode()">Remove</v-btn>
      <v-btn @click.stop="addChildNode()">Add child</v-btn>
    </v-layout>
    <tree
      v-model="selection"
      :data="treeData"
      :options="treeOptions"
      :filter="filter"
    >
      <template slot-scope="{ node }" class="node-container">
        <v-layout row>
          <v-icon class="mr-2">person</v-icon>
          <span class="node-text">{{ node.text }}</span>
        </v-layout>
      </template>
      <!-- <div slot-scope="{ node }" class="node-container">
        <div class="node-text">{{ node.text }}</div>
        <div class="node-controls">
          <a href="#" @mouseup.stop="editNode(node)">Edit</a>
          <a href="#" @mouseup.stop="removeNode(node)">Remove</a>
          <a href="#" @mouseup.stop="addChildNode(node)">Add child</a>
        </div>
      </div> -->
    </tree>
  </div>
</template>

<script>
export default {
  data: () => ({
    selection:null,
    filter: null,
    treeData: [
      {
        text: 'JS: The Right Way',
        state: { expanded: true },
        children: [
          { text: 'Getting Started'},
          { text: 'JavaScript Code Style' },
          { text: 'The Good Parts', state: { checked: true },
            children: [
              { text: 'OBJECT ORIENTED'  },
              { text: 'ANONYMOUS FUNCTIONS' },
              { text: 'FUNCTIONS AS FIRST-CLASS OBJECTS'},
              { text: 'LOOSE TYPING'}
          ]},
          { text: 'Patterns', children: [
            { text: 'DESIGN PATTERNS', children: [
              { text: 'Creational Design Patterns', children: [
                { text: 'Factory' },
                { text: 'Prototype' },
                { text: 'Mixin' },
                { text: 'Singleton' }
              ]},
              { text: 'Structural Design Patterns'}
            ]},
            { text: 'MV* PATTERNS', cildren: [
              { text: 'MVC Pattern' },
              { text: 'MVP Pattern' },
              { text: 'MVVM Pattern' }
            ]}
          ]}
        ]
      }
    ],
    treeOptions: {
      checkbox: true,
      minFetchDelay: 1000,
      fetchData: (node) => {
        return Promise.resolve(data[node.id - 1])
      },
    }
  }),
  computed: {
    treeData() {
      return this.events.concat().reverse()
    }
  },
  watch:{
    selection(val){
      console.log("selection",val);
    }
  },
  mounted(){
    this.$store.dispatch('route','/7_liquorTree');
  },
  methods:{
    editNode() {
      this.selection.checked[0].startEditing()
    },
    removeNode() {
      if (confirm('Are you sure?')) {
        this.selection.checked[0].remove()
      }
    },
    addChildNode() {
      // console.log(node);
      if (this.selection.checked[0].enabled()) {
        this.selection.checked[0].append('New Node')
      }
    }
  }
}

</script>
<style>
.tree-checkbox {
  width: 20px !important;
  height: 20px !important;
}
.tree-checkbox.checked:after {
  left: 6px !important;
  top: 1px !important;
  height: 10px !important;
  width: 4px !important;
}
/* .tree-content{
  padding-left: 0 !important;
} */
</style>